/* 原始的tooltip */
.rc-slider-tooltip {
  display: none;
}
/* progress 容器 */
div.rc-slider {
  height: 100%;
  padding: 0;
  border-radius: 0;
  width: 100%;
}
/* progress 背景 */
div.rc-slider div.rc-slider-rail {
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 0px;
}
/* progress 精度条 */
div.rc-slider div.rc-slider-track {
  height: 100%;
  background: linear-gradient(
    to right,
    rgba(9, 157, 255, 0) 0%,
    rgba(9, 157, 255, 0.6) 100%
  );
  border-radius: 0;
}
div.rc-slider.fullStatus div.rc-slider-track {
  border-radius: 0;
}
div.rc-slider div.rc-slider-step {
  height: 100%;
}
/* 滑块 */
div.rc-slider div.rc-slider-handle {
  border: none;
  background-color: #00ccff;
  width: 0px;
  box-shadow: 0 0 5px transparent !important;
  margin-left: 0;
  margin-top: 0;
  height: 100%;
  cursor: pointer;
  border-radius: 0;
}
div.rc-slider.fullStatus div.rc-slider-handle {
  opacity: 0;
}
.sliderArea {
  width: 3.25rem;
  height: 100%;
  position: relative;
}
