.scaleBox {
  width: 100%;
  height: 0.22rem;
  position: fixed;
  top: 0;
  left: 0;
  user-select: none;
  z-index: 99;
}
.scaleBox .scaleCanvas {
  width: 100%;
  height: 0.22rem;
}
.scaleBox .overlayInfo {
  display: inline-block;
  position: absolute;
  left: 0.1rem;
  top: 0;
  line-height: 0.22rem;
  font-size: 0.12rem;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
}
.scaleBox .scaleNum {
  /* display: flex;
  height: 0.22rem;
  width: 2.5rem;
  margin: auto;
  position: fixed;
  right: 0.16rem; */
  top: 0;
  position: absolute;
  width: 100%;

}
.scaleBox .scaleNum span {
  font-size: 0.12rem;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  flex: 1;
  text-align: right;
  line-height: 0.22rem;
}
.scaleBox .scaleNum.isRain span:first-child{
    position: relative;
}
.scaleBox .scaleNum.isRain span{
  padding: 0 0.02rem;
}

.scaleBox .scaleNum b>a{
  color: rgba(255, 255, 255, 1);
}

.scaleBox .scaleNum span:first-child {
  position: absolute;
}

.scaleBox .scaleNum b{
  position: absolute;
  transform: translate(-100%, 0);
  font-size: 0.12rem;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  line-height: 0.22rem;
}

.scaleBox .scaleNum b>a{
  color: rgba(255, 255, 255, 1);
}

.scaleBox .scaleBg{
  width: 100%;
  height: 0.22rem;
}