@import url(../../common/css/globalVar.css);

body{
  /* loading 图标 */
  .EnOS-loading{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 99999;
    background: rgba(1, 1, 1, 0.6);
    &:before{
      content: " ";
      color: #ffffff;
      font-size: 30px;
      width: 42px;
      height: 42px;
      position: absolute;
      left: calc(50% - 21px);
      top: calc(50% - 21px);
      background: url(../img/loading.svg);
    }
    display: none;
    &[class*=loading-]{
      display: block;
    }
  }
  color: var(--mainFontColor);
}

.text-ellipsis {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.icon-as-button {
  color: var(--highlightColor);
  cursor: pointer;
  font-size: 16px;
}

