.operationArea {
  position: fixed;
  right: 0.16rem;
  top: 0.82rem;
  width: 0.32rem;
  height: 1.48rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 999;
}
.operationArea .localIconBox {
  cursor: pointer;
  width: 0.32rem;
  height: 0.32rem;
  border-radius: 2px;
  background-color: rgba(255, 255, 255, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.operationArea .localIconBox .map_location_size {
  color: #48516c;
  font-size: 0.18rem;
}
.operationArea .searchAddress {
  cursor: pointer;
  width: 0.32rem;
  height: 0.32rem;
  border-radius: 2px;
  background-color: rgba(255, 255, 255, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.operationArea .searchAddress .map_search_size {
  color: #48516c;
  font-size: 0.18rem;
}
.operationArea .mapMode {
  cursor: pointer;
  margin-top: 10px;
  width: 0.32rem;
  height: 0.32rem;
  border-radius: 2px;
  position: relative;
}
.operationArea .mapMode {
  background: rgba(0, 0, 0, 0.6) no-repeat
    url('../../common/img/overlay/mode/mapModeHoverIcon.png') center center/100%
    100%;
}
.operationArea .mapMode:hover {
  background: rgba(0, 0, 0, 0.6) no-repeat
    url('../../common/img/overlay/mode/mapModeIcon.png') center center/100% 100%;
}
.operationArea .mapMode:hover > .modeListBox {
  display: block;
}
.operationArea .mapMode .modeListBox {
  display: none;
  position: absolute;
  bottom: 0;
  right: 30px;
  padding-right: 10px;
}
.operationArea .mapMode .modeListBox .modeList {
  padding: 12px 0;
  width: auto;
  background: rgba(13, 38, 52, 0.9);
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.3);
  border-radius: 4px;
}
.operationArea .mapMode .modeListBox .modeList .item {
  width: 100%;
  height: 24px;
  font-size: 12px;
  font-family: MicrosoftYaHei;
  color: rgba(233, 246, 255, 1);
  text-align: center;
  padding: 0 12px;
  display: flex;
  align-items: center;
}
.operationArea .mapMode .modeListBox .modeList .item:hover {
  background: rgba(255, 255, 255, 0.05);
}
.operationArea .mapMode .modeListBox .modeList .item .radio {
  display: inline-block;
  margin-right: 8px;
  width: 16px;
  height: 16px;
  border: 1px solid rgba(0, 204, 255, 0.6);
  box-sizing: border-box;
  position: relative;
  border-radius: 16px;
}
.operationArea .mapMode .modeListBox .modeList .item span {
  vertical-align: middle;
  white-space: nowrap;
}
.operationArea .mapMode .modeListBox .modeList .item .radio.selected::after {
  content: '';
  display: inline-block;
  width: 8px;
  height: 8px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #00ccff;
  border-radius: 8px;
}
