/* 日期区域 */
.TicksLabel {
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
}
.TicksLabel .labelBox {
  padding: 0.08rem 0 0.08rem 0.08rem;
  height: 100%;
  flex: 1;
  text-align: center;
  position: relative;
}
.TicksLabel .labelBox:not(:last-child)::after {
  content: '';
  display: inline-block;
  width: 1px;
  height: 8px;
  background-color: rgba(255, 255, 255, 0.5);
  position: absolute;
  bottom: 0;
  right: 0px;
}
.TicksLabel .labelBox span {
  display: block;
  font-size: 0.12rem;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  line-height: 1;
  text-align: left;
}
.TicksLabel .labelBox span.dateText {
  margin-bottom: 0.02rem;
  color: rgba(255, 255, 255, 1);
}
.TicksLabel .labelBox span.weekText {
  font-size: 0.1rem;
  color: rgba(255, 255, 255, 0.6);
}
