.map-container {
  height: 100%;
  position: relative;
  z-index: 9;
}
.leaflet-container {
  height: 100%;
}
.leaflet-bottom.leaflet-right {
  top: 1.24rem;
  right: 0.16rem;
}
.leaflet-bottom.leaflet-right .leaflet-control-attribution {
  display: none;
}
.leaflet-bottom.leaflet-right .leaflet-control-zoom {
  margin-bottom: 0;
  margin-right: 0;
  border: none;
}
.leaflet-bottom.leaflet-right .leaflet-control-zoom a {
  position: relative;
  font-size: 0;
  width: 0.32rem;
  height: 0.32rem;
  box-sizing: border-box;
}
.leaflet-bottom.leaflet-right .leaflet-control-zoom .leaflet-control-zoom-in {
  background-color: rgba(255, 255, 255, 1);
  border-bottom: 0.02rem solid #edf3fc;
}
.leaflet-bottom.leaflet-right
  .leaflet-control-zoom
  .leaflet-control-zoom-in:before {
  content: '\e908';
  font-family: 'mapicon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #48516c;
  font-size: 0.18rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.leaflet-bottom.leaflet-right .leaflet-control-zoom .leaflet-control-zoom-out {
  background-color: rgba(255, 255, 255, 1);
}
.leaflet-bottom.leaflet-right
  .leaflet-control-zoom
  .leaflet-control-zoom-out:before {
  content: '\e909';
  font-family: 'mapicon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #48516c;
  font-size: 0.18rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
