.tooltipBox {
  position: absolute;
  bottom: 0.45rem;
  z-index: 1070;
  display: block;
  font-family: PingFangTC-Regular;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  text-align: start;
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  word-break: normal;
  word-spacing: normal;
  word-wrap: normal;
  white-space: normal;
  filter: alpha(opacity=0);
  opacity: 1;
  line-break: auto;
  user-select: none;
  transform: translateX(-50%);
}
.tooltipBox .tooltip_inner {
  display: inline-block;
  cursor: pointer;
  padding: 0.06rem 0.08rem;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 2px;
  font-size: 0.12rem;
  max-width: 2rem;
  color: #fff;
  text-align: center;
  line-height: 1;
}
.tooltipBox .tooltip_arrow {
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  bottom: -0.05rem;
  left: 50%;
  margin-left: -0.05rem;
  border-width: 0.05rem 0.05rem 0;
  border-top-color: rgba(0, 0, 0, 0.6);
}
