.message {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: transparent;
  z-index: 10000;
}
.message.show {
  display: block;
}
.message.hide {
  display: none;
}
.message .progress-bar {
  position: fixed;
  display: inline-block;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: calc(100% - 0.64rem);
}
@-webkit-keyframes rotation {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes rotation {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.message .progress-bar .content {
  padding: 0.1rem 0.16rem;
  background: #000000;
  -webkit-box-shadow: 0rem 0.06rem 0.16rem 0rem rgba(0, 0, 1, .3);
          box-shadow: 0rem 0.06rem 0.16rem 0rem rgba(0, 0, 1, .3);
  border-radius: 0.2rem;
  font-size: 0.15rem;
  font-weight: 600;
  color: #ffffff;
  animation: rotation 3s ease-in-out infinite;
  -moz-animation: rotation 3s ease-in-out infinite;
  -webkit-animation: rotation 3s ease-in-out infinite;
  -o-animation: rotation 3s ease-in-out infinite;
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  margin: auto;
}
