* {
  margin: 0px;
  padding: 0px;
}
li {
  list-style: none;
}
.line:before {
  content: "";
  display: block;
  margin-left: 15px;
  width: 100%;
  height: 1px;
  box-sizing: border-box;
  background-color: #dfdee6;
}
.farmlist {
  background-color: #ffffff;
  height: 100%;
  text-align: left;
}
.farmlist .searchArea {
  padding-top:0.2rem;
  /* margin-bottom: 0.08rem; */
  width: 100%;
  border-bottom: 1px solid #F1F5FA;
}
.farmlist .line {
  width: 100%;
  overflow: hidden;
}
.farmlist .result{
  position: relative;
  width: 100%;
  height:calc(100% - 0.44rem);
}
.farmlist .result div{
  padding-top:0.08rem;
}
.farmlist .list {
  padding: 0 0.2rem 0 0.47rem;
  width: 100%;
  height:100%;
  box-sizing: border-box;
  background-color: #ffffff;
}
.farmlist .list .item {
  padding: 0.16rem 0 0.17rem 0;
  height: 0.8rem;
  width: 100%;
  position: relative;
  box-sizing: border-box;
  border-bottom: 1px solid #F6F6FA;
}
.farmlist .list .item:last-child{
  border-bottom:none;
}
.farmlist .list .item .name {
  font-size: 0.15rem;
  color: #26272E;
  font-weight: 500;
  height: 0.21rem;
  line-height: 0.21rem;
  position: relative;
}
.farmlist .list .item .name .rightIcon{
  font-size: 0.13rem;
  color:#000000;
  position: absolute;
  top: 50%;
  right:0;
  transform: translateY(-50%);
}
.farmlist .list .item .name .dot {
  position: absolute;
  top: 50%;
  left: -0.27rem;
  transform: translateY(-50%);
  width: 0.16rem;
  height: 0.16rem;
  border-radius: 0.16rem;
  background: white;
  border: 0.03rem solid;
}
.farmlist .list .item .name .dotRed {
  border-color:#e05d66;
  box-shadow:0px 0.04rem 0.04rem 1px #e05d663f;
}
.farmlist .list .item .name .dotYellow {
  border-color:#eeaa3d;
  box-shadow:0px 0.04rem 0.04rem 1px #eeaa3d3f;
}
.farmlist .list .item .name .dotGreen {
  border-color:#68d9ae;
  box-shadow:0px 0.04rem 0.04rem 1px #68d9ae3f;
}
.farmlist .list .item .name .dotPurple {
  border-color:#8143f0;
  box-shadow:0px 0.04rem 0.04rem 1px #8143f03f;
}
.farmlist .list .item .desc {
  margin-top: 0.08rem;
  font-size: 0.13rem;
  color: #A0A4AD;
  line-height: 0.18rem;
}
.farmlist .list .item .desc .unit {
  margin-right: 8px;
}
