  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -35px;
  width: 15px;
  height: 15px;
} */
.farmlist .list .am-list-item {
    padding-left: 0px;
}

.farmlist .am-search .am-search-cancel {
    color: #A0A4AD;
}

.farmlist .history {
    position: relative;
    width: 100%;
    height: 88%;
}

.farmlist .history .deleteIconArea {
    margin: 0.2rem 0;
    width: 100%;
    text-align: right;
    padding-right: 0.21rem;
    box-sizing: border-box;
}

.farmlist .history .deleteIconArea .deleteIcon {
    font-size: 0.16rem;
    color: #3C3E47;
}

.farmlist .history .historyBox {
    padding: 0 0.16rem;
    width: 100%;
}

.farmlist .history .historyBox .item {
    margin-right: 0.12rem;
    margin-bottom: 0.12rem;
    display: inline-block;
    height: 0.28rem;
    line-height: 0.28rem;
    padding: 0 0.16rem 0 0.28rem;
    font-size: 0.12rem;
    color: #FFFFFF;
    background: linear-gradient(270deg, rgba(41, 118, 255, 1) 0%, rgba(51, 167, 255, 1) 100%);
    box-shadow: 0px 6px 16px 0px rgba(42, 134, 255, 0.3);
    border-radius: 0.16rem;
    position: relative;
}

.farmlist .history .historyBox .item .timeIcon {
    font-size: 0.12rem;
    color: #FFFFFF;
    position: absolute;
    left: 0.08rem;
    top: 50%;
    transform: translateY(-50%);
}

.tangram-suggestion-main {
    width: 100% !important;
    /* display: block !important; */
    left: 0px !important;
    top: 0.65rem !important;
    position:fixed !important;
}

.tangram-suggestion-main .tangram-suggestion {
    border: none;
}

.tangram-suggestion-main .tangram-suggestion div {
    display: none;
}

.tangram-suggestion-main .tangram-suggestion table {
    width: 80.8%;
    margin: 0 5.3% 0 13.9%;
}

.tangram-suggestion-main .tangram-suggestion table tr {
    border-bottom: 1px solid #F6F6FA;
    display: block;
}

.tangram-suggestion-main .tangram-suggestion table tr td {
    padding: 0.16rem 0;
    height: 0.8rem;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    /* background:url(../assets/images/location_copy@2x.png) no-repeat 0px center/0.12rem 0.16rem; */
    overflow: unset;
    position: relative;
}

.tangram-suggestion-main .tangram-suggestion table tr td::before {
    content: "";
    display: block;
    width: 0.12rem;
    height: 0.16rem;
    position: absolute;
    top: 50%;
    margin-top: -0.08rem;
    left: -0.28rem;
    background: url(../../common/img/addressList/location.png) no-repeat center center/100% 100%;
}

.tangram-suggestion-main .tangram-suggestion table tr td i {
    background: none;
    font-weight: 400;
    color: #26272E;
    padding: 0;
    line-height: 1;
    font-size: 0.15rem;
}

.tangram-suggestion-main .tangram-suggestion table tr td i b {
    font-weight: 400;
}

.tangram-suggestion-main .tangram-suggestion table tr td i span {
    display: block;
    font-size: 0.13rem;
    margin-top: 0.18rem;
    line-height: 0.18rem;
}

.tangram-suggestion-main .tangram-suggestion table tr td i span.tangram-suggestion-grey {
    color: #A0A4AD;
}
