.login-container{
	background-image: url(../../static/media/env_bg.bc998953.jpg);
	background-size: cover;
    width: 100%;
    height: 100vh;
}
    
    .login-container .logo-container{
        position: absolute; 
		left:10%;
		width:25%;
		top:6px;
		bottom:0;
    }
    
    .login-container .logo{
		width:100%;
		margin:auto 0;
        position: absolute;
        top: calc(50% - 85px);
	}
    
    .login-container .twofac{
		position: absolute;
		width:400px;
		height:348px;
		right: calc(50% - 200px);
		top:calc(50% - 174px);
		background: rgba(0, 0, 0, .6);
	}
    
    .login-container .twofac .title{
			width: 320px;
			height: 56px;
			font-size: 20px;
			margin-left: 40px;
			margin-right: 40px;
			margin-top: 32px;
		}
    
    .login-container .twofac input[type=radio]:not(old) {
			width     : 2em;
			margin    : 0;
			padding   : 0;
		    font-size : 1em;
		    opacity   : 0;
		}
    
    .login-container .twofac input[type=radio]:not(old) + label {
			display: inline-block;
			margin-left: -2em;
			line-height: 1.5em;
		}
    
    .login-container .twofac input[type=radio]:not(old) + label > span{
			display          : inline-block;
			width            : 0.875em;
			height           : 0.875em;
			margin           : 0.25em 0.5em 0.25em 0.25em;
			border           : solid 1px #00ccff;
			border-radius    : 50%;
/*			background-image :    -moz-linear-gradient(rgb(240,240,240),rgb(224,224,224));
			background-image :     -ms-linear-gradient(rgb(240,240,240),rgb(224,224,224));
			background-image :      -o-linear-gradient(rgb(240,240,240),rgb(224,224,224));
			background-image : -webkit-linear-gradient(rgb(240,240,240),rgb(224,224,224));
			background-image :         linear-gradient(rgb(240,240,240),rgb(224,224,224));*/
			vertical-align   : bottom;
		}
    
    .login-container .twofac input[type=radio]:not(old):checked + label > span{
/*		  background-image :    -moz-linear-gradient(rgb(224,224,224),rgb(240,240,240));
		  background-image :     -ms-linear-gradient(rgb(224,224,224),rgb(240,240,240));
		  background-image :      -o-linear-gradient(rgb(224,224,224),rgb(240,240,240));
		  background-image : -webkit-linear-gradient(rgb(224,224,224),rgb(240,240,240));
		  background-image :         linear-gradient(rgb(224,224,224),rgb(240,240,240));*/
		}
    
    .login-container .twofac input[type=radio]:not(old):checked + label > span > span{
		  display          : block;
		  width            : 0.5em;
		  height           : 0.5em;
		  margin           : 0.125em;
		  border           : 0.0625em solid #00ccff;
		  border-radius    : 50%;
		  background-color : #00ccff;
/*		  background-image :    -moz-linear-gradient(rgb(179,217,140),rgb(153,204,102));
		  background-image :     -ms-linear-gradient(rgb(179,217,140),rgb(153,204,102));
		  background-image :      -o-linear-gradient(rgb(179,217,140),rgb(153,204,102));
		  background-image : -webkit-linear-gradient(rgb(179,217,140),rgb(153,204,102));
		  background-image :         linear-gradient(rgb(179,217,140),rgb(153,204,102));*/
		}
    
    .login-container .twofac .vald{
			margin-left: 45px;
			margin-top: 36px;
			font-size: 16px;
		}
    
    .login-container .twofac .hint{
			position: absolute;
			cursor: pointer;
			text-align: right;
			margin-top: 32px;
			width: 70px;
			margin-left: 280px;
			color: #00ccff;
		}
    
    .login-container .twofac .valdinput{
			margin-left: 40px;
			padding-left: 10px;
			height: 38px;
			width: 310px;
			border-radius: 4px;
			background-color: rgba(0, 204, 255, .1);
			border: solid 1px #00ccff;
			margin-top: 20px;
			color: #ffffff;
		}
    
    .login-container .twofac .backclose{
            float: right;
            margin-top: 10px;
            margin-right: 10px;
            cursor: pointer;
        }
    
    .login-container .twofac .valdsubmit{
			width: 320px;
			height: 44px;
			opacity: .5;
			border-radius: 4px;
			background-color: #00ccff;
			margin-left: 40px;
			font-size: 16px;
			color: #053447;
			margin-top: 16px;
			cursor: pointer;
			border: none
		}
    
    .login-container .twofac .valdsubmit:focus {
			-webkit-box-shadow: 0 0 15px #00ccff;
			        box-shadow: 0 0 15px #00ccff;
}
    
    .login-container .twofac .valdsubmit:disabled{
			cursor: not-allowed;
		}
    
    .login-container .twofac .retry{
			margin-left: 40px;
			font-size: 12px;
			color: #ff5555;
			text-align: left;
			margin-top: 5px;
			display: inline-block;
		}
    
    .login-container .login{
		position: absolute;
		width:60%;
		right: 0%;
		top:calc(50% - 150px);
		height: 300px;
		background: rgba(0, 0, 0, .4);
		border-radius: 10px 0 0 10px;


	}
    
    .login-container .login .form{
			position: absolute;
			width:360px;
			height: 230px;
			left:80px;
			top: 40px;
			font-size: 14px;
			color: #fff;
		}
    
    .login-container .login .form input{
				border-radius: 5px;
				font-size: 22px;
				background: rgb(250, 255, 189);
				margin: 6px 0 20px 0;
				border: none;
				outline: none;
				padding: 5px 5px 6px 40px;
				color: #000;
				width: 100%;
				-webkit-box-sizing: border-box;
				        box-sizing: border-box;
				line-height: 26px;
			}
    
    .login-container .login .form .submit{
				text-align: center;
				cursor: pointer;
				background: #ffbd3d;
				height: 40px;
				border-radius: 6px;
				font-size: 16px;
				line-height: 40px;
				color: black;
				-webkit-box-sizing: border-box;
				        box-sizing: border-box;
				display: block;
				width: 100%;
				border: none;
				outline: none;
                font-family: inherit
			}
    
    .login-container .login .form .submit:focus {
				-webkit-box-shadow: 0 0 15px #ffbd3d;
				        box-shadow: 0 0 15px #ffbd3d;
}
    
    .login-container .copy{
		position: absolute;
		left:10px;
		bottom:10px;
		font-size: 12px;
		color:#888;
	}
    
    .login-container .user,
    .login-container .pwd{
        position: relative;
    }
    
    .login-container .user:before,
    .login-container .pwd:before{
        content: "";
        position: absolute;
        top: 10px;
        left: 2px;
        display: block;
        width: 30px;
        height: 30px;
        background-repeat: no-repeat;
    }
    
    .login-container .user:before{
        background-image: url(data:image/png;base64,);
    }
    
    .login-container .pwd:before{
        background-image: url(data:image/png;base64,)
    }
    
    .login-container .lang{
        text-align: right;
        font-size: 16px;
    }
    
    .login-container .lang-choose{
        cursor: pointer;
        color: hsla(0, 0%, 86%, .4)
    }
    
    .login-container .lang-choose:hover{
	color: #fff;
}
    
    .login-container .lang-choose-active{
        cursor: pointer;
        color: #fff;
    }
    
    .login-container .logo-bg{
        position: absolute;
        bottom: 0;
        right: 0;
        overflow: hidden;
    }
    
    .login-container .label{
        height: 16px;
        display: block;
    }
    
    .login-container .findpwd{
        text-align: right;
        cursor: pointer;
        color: #00ccff;
        padding-top: 6px;
    }
