.weather-container {
  height: 100%;
}
.weather-container .WeatherArea {
  position: relative;
  z-index: 9;
}
.weather-container .bottomArea {
  width: 100%;
  height: 0.4rem;
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99;
  font-size: 0;
}
.weather-container .bottomArea .timeSliderArea {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  /* background-color: rgba(0, 0, 0, 0.6); */
}
.weather-container .bottomArea .scaleBarArea {
  display: inline-block;
  position: relative;
  padding: 13px 20px 25px 15px;
  width: 34%;
  height: 100%;
  box-sizing: border-box;
  vertical-align: top;
}
