@import url(./common/css/globalVar.css);
*{
  margin:0;
  padding:0;
}
html, body, #root{
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color:white;
}

ul, li{
  list-style-type: none;
}
body a{
  text-decoration: none;
  color: #0058ff;
}

body {
  font-family: Helvetica, Tahoma, Arial, "Microsoft YaHei", "微软雅黑", "PingFang", "苹方", STHeiti, "黑体", STXihei, "华文细黑", SimSun, "宋体", sans-serif;
