.overlayControlArea .mask {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 998;
  background-color: rgba(0, 0, 0, 0.3);
}
.overlayControlArea .contentBox {
  position: fixed;
  left: 100%;
  z-index: 999;
  transition: transform 0.3s;
}
/* 入场动画： */
.overlayControlArea.fade.enter .contentBox {
  transform: translateX(0%);
}
.overlayControlArea.fade.enter-active .contentBox {
  transform: translateX(-100%);
}
.overlayControlArea.fade.enter-done .contentBox {
  transform: translateX(-100%);
}
.overlayControlArea.fade.enter-done .mask {
  display: block;
}
/* 出场动画： */
.overlayControlArea.fade.exit .contentBox {
  transform: translateX(-100%);
}
.overlayControlArea.fade.exit-active .contentBox {
  transform: translateX(0%);
}
.overlayControlArea.fade.exit-done .contentBox {
  transform: translateX(0%);
}
.overlayControlArea.fade.exit-done .mask {
  display: none;
}
/* 图层图标 start */
.overlayControlArea .overlayIcon {
  display: inline-block;
  width: 0.18rem;
  height: 0.18rem;
  background-color: #48516c;
  -webkit-mask-size: cover;
  -webkit-mask-repeat: no-repeat;
}
.overlayIcon.windIcon {
  -webkit-mask-image: url(../../common/img/overlay/windSvg.svg);
}
.overlayIcon.tempIcon {
  -webkit-mask-image: url(../../common/img/overlay/tempSvg.svg);
}
.overlayIcon.lightningIcon {
  -webkit-mask-image: url(../../common/img/overlay/lightSvg.svg);
}
.overlayIcon.humidityIcon {
  -webkit-mask-image: url(../../common/img/overlay/humiditySvg.svg);
}
.overlayIcon.rainIcon {
  -webkit-mask-image: url(../../common/img/overlay/rainSvg.svg);
}
/* 图层图标 end */
.overlayControlArea {
  position: fixed;
  z-index: 999;
}
.overlayControlArea .contentBox {
  padding-left: 0.2rem;
  width: 2.4rem;
  height: 100%;
  background-color: rgba(255, 255, 255, 1);
  border-top: 1px solid rgba(235, 239, 245, 1);
  box-sizing: border-box;
}
/* 地图切换 start */
.overlayControlArea .contentBox .mapModeArea {
  padding-bottom: 0.16rem;
  width: 100%;
  height: auto;
  border-bottom: 1px solid rgba(235, 239, 245, 1);
}
.overlayControlArea .contentBox .mapModeArea .title {
  margin-top: 0.17rem;
  margin-bottom: 0.14rem;
  font-size: 0.14rem;
  font-family: PingFangSC;
  font-weight: 500;
  color: rgba(160, 164, 173, 1);
  line-height: 1;
}
.overlayControlArea .contentBox .mapModeArea .mapModeBox {
  width: 100%;
  font-size: 0;
}
.overlayControlArea .contentBox .mapModeArea .mapModeBox .item {
  display: inline-block;
  margin-right: 0.16rem;
  width: auto;
  height: auto;
  text-align: center;
}
.overlayControlArea .contentBox .mapModeArea .mapModeBox .item.active .modeImg {
  border: 0.04rem solid rgba(9, 157, 255, 1);
}
.overlayControlArea
  .contentBox
  .mapModeArea
  .mapModeBox
  .item.active
  .modeText {
  color: rgba(9, 157, 255, 1);
}
.overlayControlArea .contentBox .mapModeArea .mapModeBox .item .modeImg {
  display: block;
  margin-bottom: 0.12rem;
  border: 0.04rem solid white;
  border-radius: 0.02rem;
  width: 0.8rem;
  height: 0.6rem;
}
.overlayControlArea .contentBox .mapModeArea .mapModeBox .item .modeText {
  font-size: 0.15rem;
  font-family: PingFangSC;
  font-weight: 500;
  color: rgba(41, 43, 66, 1);
  line-height: 1;
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  display: inline-block;
  white-space: nowrap;
}
/* 地图切换 end */
.overlayControlArea .sourceArea .curentSource {
