  width: auto;
  height: 36px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 2px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}
.overlayControlArea .sourceArea .curentSource .sourceLabel {
  margin: 0;
  padding-right: 12px;
  font-size: 12px;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.74);
}
.overlayControlArea .sourceArea .curentSource .sourceLabel span {
  font-size: 12px;
  display: inline-block;
}
.overlayControlArea .sourceArea .curentSource .sourceLabel .sourceText {
  font-size: 12px;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  line-height: 17px;
}
.overlayControlArea .sourceArea .curentSource .arrowBox {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 25px;
  height: 20px;
  border-left: 2px solid rgba(255, 255, 255, 0.0893);
  box-sizing: border-box;
}
.overlayControlArea .sourceArea .curentSource .arrowBox .arrow_down {
  width: 12px;
  height: 12px;
  background-color: white;
  -webkit-mask-image: url(../../common/img/arrow_down.svg);
  cursor: pointer;
}
.overlayControlArea .sourceArea .optionArea {
  padding: 0 16px;
  width: 168px;
  background: rgba(12, 65, 85, 1);
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.35);
  border-radius: 2px;
}
.overlayControlArea .sourceArea .optionArea .sourceItem {
  cursor: pointer;
  height: 35px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.overlayControlArea .sourceArea .optionArea .sourceItem:hover {
  color: rgba(255, 255, 255, 1);
}
.overlayControlArea .sourceArea .optionArea .sourceItem:last-child {
  border-bottom: none;
}
.overlayControlArea .overlayArea {
  display: inline-block;
  width: 100%;
  border-bottom: 1px solid rgba(235, 239, 245, 1);
  padding-bottom: 0.04rem;
}
.overlayControlArea .overlayArea .title {
  margin-top: 0.17rem;
  margin-bottom: 0.14rem;
  font-size: 0.14rem;
  font-family: PingFangSC;
  font-weight: 500;
  color: rgba(160, 164, 173, 1);
  line-height: 1;
}
.overlayControlArea .overlayArea .overlayBox {
  display: flex;
  flex-direction: column;
}
.overlayControlArea .overlayArea .overlayBox .overlayItem {
  margin-bottom: 0.12rem;
}
.overlayControlArea .overlayArea .overlayBox .overlayItem.focus {
  /* background-color: rgba(0, 204, 255, 0.7956); */
}
.overlayControlArea
  .overlayArea
  .overlayBox
  .overlayItem.focus
  .overlayCont
  .overlayIcon {
  background-color: rgba(255, 255, 255, 1);
}
.overlayControlArea
  .overlayArea
  .overlayBox
  .overlayItem.focus
  .overlayCont
  .overlayName {
  color: #099dff;
}
.overlayControlArea .overlayArea .overlayBox .overlayItem .overlayCont {
  cursor: pointer;
  padding: 0 4px 0 2px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 36px;
  box-sizing: border-box;
}
.overlayControlArea
  .overlayArea
  .overlayBox
  .overlayItem
  .overlayCont
  .radioBox {
  width: 0.32rem;
  height: 0.32rem;
  border-radius: 0.32rem;
  border: 0.01rem solid rgba(72, 81, 108, 1);
  text-align: center;
  line-height: 0.32rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.12rem;
}
.overlayControlArea
  .overlayArea
  .overlayBox
  .overlayItem.focus
  .overlayCont
  .radioBox {
  background-color: #099dff;
  border: 0.01rem solid #099dff;
}
.overlayControlArea
  .overlayArea
  .overlayBox
  .overlayItem
  .overlayCont
  .overlayName {
  font-size: 0.14rem;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(41, 43, 66, 1);
}
.overlayControlArea .switchArea {
  padding-top: 0.26rem;
  display: flex;
  align-items: center;
}
.overlayControlArea .switchArea .switch {
  margin: 0;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  position: relative;
  width: 0.4rem;
  height: 0.24rem;
  outline: none !important;
  border-radius: 0.16rem;
  border: 0.01rem;
  box-sizing: border-box;
  background-color: #dfdfdf;
  transition: background-color 0.1s, border 0.1s;
}

.overlayControlArea .switchArea .switch::before {
  content: ' ';
  position: absolute;
  width: 0.4rem;
  height: 0.24rem;
  border: 0.01rem solid #dfdfdf;
  border-radius: 0.16rem;
  background-color: #bbbfc7;
  transition: transform 0.35s cubic-bezier(0.45, 1, 0.4, 1);
  box-sizing: border-box;
}

.overlayControlArea .switchArea .switch::after {
  content: ' ';
  position: absolute;
  top: 0.02rem;
  left: 0.02rem;
  width: 0.2rem;
  height: 0.2rem;
  border-radius: 0.15rem;
  background-color: #ffffff;
  box-shadow: 0 0.01rem 0.03rem rgba(0, 0, 0, 0.4);
  transition: transform 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35);
}

.overlayControlArea .switchArea .switch:checked {
  border-color: #099dff;
  background-color: #099dff;
}

.overlayControlArea .switchArea .switch:checked::before {
  transform: scale(0);
}

.overlayControlArea .switchArea .switch:checked::after {
  transform: translateX(0.17rem);
}
.overlayControlArea .switchArea .switchText {
  margin-left: 0.1rem;
  font-size: 0.15rem;
  font-family: PingFangSC-Medium;
  font-weight: 500;
  color: rgba(41, 43, 66, 1);
}
