.searchSite_container{
    width:100%;
    height:100%;
    font-size:0.12rem;
}
.searchSite_container .wrongArea{
    width:100%;
    padding:0.34rem 0 0.14rem 0.23rem;
    text-align: left;
}
.searchSite_container .wrongArea .wrongIcon{
    font-size: 0.16rem;
    color:rgba(0,0,0,0.65);
}
.searchSite_container .tabArea{
    padding:0 0.16rem;
    width:100%;
    height:0.45rem;
    text-align: left;
    border-bottom: 0.01rem solid #F1F5FA;
    box-sizing: border-box;
}
.searchSite_container .tabArea .tabItem{
    margin-right:0.4rem;
    display: inline-block;
    /*width:0.8rem;*/
    height:0.45rem;
    font-size: 0.18rem;
    color:#A0A4AD;
    font-weight:600;
    line-height:0.45rem;
    text-align: center;
    position: relative;
}
.searchSite_container .tabArea .tabItem .tabText{
    font-size: 0.18rem;
    line-height: 0.45rem;
}
.searchSite_container .tabArea .tabItem.active{
    color:#26272E;
}
.searchSite_container .tabArea .tabItem.active .curCss{
    display: inline-block;
    width:50%;
    height:0.05rem;
    border-radius: 0.15rem;
    background:linear-gradient(270deg,rgba(41,118,255,1) 0%,rgba(21,152,250,1) 100%);
    position: absolute;
    bottom:0rem;
    left:50%;
    transform: translateX(-50%);
}
.searchSite_container .searchArea{
    margin-top:0.2rem;
}
.searchSite_container .locationTab{
    margin-top:0.08rem;
    width:100%;
    height:74.21%;
    position: relative;
}
.searchSite_container .locationTab ul{
    padding-bottom:0.2rem;
    width: 80.8%;
    height:100%;
    margin: 0 5.3% 0 13.9%;
}
.searchSite_container .locationTab ul li{
    border-bottom: 1px solid #F6F6FA;
    padding:0.16rem 0;
    text-align: left;
    position: relative;
}
.searchSite_container .locationTab ul li .title{
    margin-bottom:0.08rem;
    height:0.21rem;
    font-size:0.15rem;
    font-family:PingFangSC-Regular;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    font-weight:400;
    color:rgba(38,39,46,1);
    line-height:0.21rem;
}
.searchSite_container .locationTab ul li .detail{
    height:0.18rem;
    font-size:0.13rem;
    font-family:PingFangSC-Regular;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    font-weight:400;
    color:rgba(160,164,173,1);
    line-height:0.18rem;
}
.searchSite_container .locationTab ul li .locationIcon{
    width:0.12rem;
    height:0.16rem;
    position: absolute;
    left:-0.28rem;
    top:50%;
    margin-top:-0.08rem;
}
.searchSite_container .locationTab ul li.clearAddHostory{
    border-bottom:none;
    text-align: center;
    height:0.44rem;
    padding:0;
    position: relative;
    display: flex;
    justify-content: center;
}
.searchSite_container .locationTab ul li.clearAddHostory .text{
    /* margin:0 1.12rem 0 1.07rem; */
    height:0.44rem;
    font-size:0.14rem;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(60,62,71,1);
    line-height:0.44rem;
    position: relative;
}
.searchSite_container .locationTab ul li.clearAddHostory .deleteIcon{
    width:0.16rem;
    height:0.16rem;
    position: absolute;
    left:-0.25rem;
    top:50%;
    margin-top:-0.08rem;
}
.searchSite_container .farmTab{
    height:62%;
}
.searchSite_container .treeArea{
    height: 100%;
    text-align: left;
    padding-bottom: 0.08rem;
    position: relative;
    box-sizing: border-box;
    width: 89.33%;
    margin: 0.043rem auto 0;
}
.searchSite_container .treeArea .ant-tree{
    font-size: 0.16rem;
    line-height: 0.5rem;
    color:#26272E;
}
.searchSite_container .treeArea .ant-tree li{
    padding: 0px 0;
}
.ant-tree-title{
    height: .5rem;
    font-size: .16rem;
//font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(60,62,71,1);
}
.searchSite_container .treeArea .ant-tree li .ant-tree-node-content-wrapper:hover {
    background-color: rgba(255,255,255,1);
}
.searchSite_container .treeArea .ant-tree li .ant-tree-node-content-wrapper.ant-tree-node-selected{
    background-color: rgba(255,255,255,1);
}
.searchSite_container .treeArea .ant-tree li .ant-tree-node-content-wrapper.ant-tree-node-selected .ant-tree-title{
      background: linear-gradient(270deg,rgba(41,118,255,1) 0%,rgba(51,167,255,1) 100%);
      box-shadow: 0 0.06rem 0.16rem 0 rgba(42, 134, 255, 0.3);
      border-radius: 0.04rem;
      color: rgba(255, 255, 255, 1);
      padding: 0.08rem;
  }
.searchSite_container .treeArea .ant-tree li .ant-tree-node-content-wrapper{
    height: .5rem;
    line-height: .5rem;
    margin: 0;
    width: calc(100% - .3rem);
    padding: 0;
}
.searchSite_container .treeArea .ant-tree li .ant-tree-node-content-wrapper .ant-tree-title{
    padding: 0 0.08rem;
}
.searchSite_container .treeArea .ant-tree li .ant-tree-node-content-wrapper.ant-tree-node-content-wrapper-normal.ant-tree-node-selected{
    /* background:linear-gradient(270deg,rgba(41,118,255,1) 0%,rgba(51,167,255,1) 100%); */
    /* box-shadow:0px 6px 16px 0px rgba(42,134,255,0.3); */
    background: transparent;
    box-shadow: none;
    border-radius:4px;
    color: white;
}
.searchSite_container .treeArea .ant-tree.ant-tree-show-line li:not(:last-child):before{
    border-left:none;
}
/* .ant-tree.ant-tree-show-line li span.ant-tree-switcher.ant-tree-switcher-noop:after{
    font-size:0rem;
} */
.ant-tree li span.ant-tree-switcher.ant-tree-switcher-noop{
    /*visibility: hidden;*/
}
.searchSite_container .confirmArea{
    position: fixed;
    bottom:0rem;
    border-top:1px solid #EBEFF5;
    width:100%;
    display: flex;
    flex-direction:row;
    flex-wrap:nowrap;
    background-color: #FFFFFF;
}
.searchSite_container .confirmArea .btn{
    height:0.5rem;
    flex: 1 1 auto;
    font-size:0.15rem;
    color:#000000;
    line-height: 0.5rem;
    text-align: center;
    font-weight: 600;
    margin-bottom: 0rem;
}
.searchSite_container .confirmArea .btn.cancel{
    border-right:1px solid #EBEFF5;
    box-sizing: border-box
}

.searchSite_container .treeArea .ant-tree .ant-tree.ant-tree-show-line li span.ant-tree-switcher {
    background: transparent;
    color: rgba(0, 0, 0, 0.45);
    height: .5rem;
    line-height: .5rem;
    margin-top: 0;
    width: 0.04rem;
}

.searchSite_container .treeArea .ant-tree .ant-tree.ant-tree-show-line li span.ant-tree-switcher.ant-tree-switcher-noop:after {
    content: ' ';
    border-bottom: 1px dashed rgba(215,219,224, 1);
    width: 0.8rem;
    position: absolute;
    top: 0.8789rem;
    left: -0.7690rem;
    height: 0.8rem;
}

.searchSite_container .treeArea .ant-tree .ant-tree > li:last-child {
    padding-bottom: 0;
}

.ant-tree > li:first-child {
    padding-top: 0;
}


.map_minus_square_s:after{
    content: ' ';
    border: none;
    border-bottom: 1px dashed #d7dbe0;
    width: 0.12rem;
    position: absolute;
    top: 0.25rem;
    z-index: 2;
    left: -0.12rem;
}

.map_plus_square_s:after{
    content: ' ';
    border: none;
    border-bottom: 1px dashed #d7dbe0;
    width: 0.12rem;
    position: absolute;
    top: 0.25rem;
    z-index: 2;
    left: -0.12rem;
}
.map_plus_square_s:before {
    content: "\e934";
}
.map_minus_square_s:before {
    content: "\e935";
}
.treeIcon{
    background: #fff;
    color: rgba(38, 39, 46, 1);
    height: .5rem;
    line-height: .5rem;
    margin-top: 0;
    font-size: 0.14rem;
    margin-right: 0.08rem;
}
.anticon{
    display: none;
}
.ant-tree.ant-tree-show-line li:not(:last-child):before{
    content: ' ';
    width: 0.01rem;
    border: none;
    border-left: 1px dashed #d7dbe0 !important;
    height: 100%;
    position: absolute;
    margin: 0 !important;
    top: -0.24rem !important;
    z-index: 2;
    left: -0.11rem !important;
}
.ant-tree.ant-tree-show-line li span.ant-tree-switcher.ant-tree-switcher-noop:after {
    content: ' ';
    border-bottom: 1px dashed #d7dbe0;
    width: 0.128rem;
    position: absolute;
    top: 0.14rem;
    left: -0.11rem;
    height: 0.128rem;
}
.b-scroll-content {
     margin: 0 0;
    width: 91.47%;
    box-sizing: border-box;
}
.ant-tree.ant-tree-show-line li span.ant-tree-switcher {
    background: transparent;
    color: rgba(0, 0, 0, 0.45);
    height: .5rem;
    line-height: .5rem;
    margin-top: 0;
    width: 0.04rem;
}
.ant-tree.ant-tree-show-line li:last-child:before {
    content: ' ';
    width: 0.1rem;
    border: none;
    border-left: 1px dashed #d7dbe0;
    height: .4731rem;
    position: absolute;
    top: -0.24rem !important;
    z-index: 2;
    left: -0.11rem !important;
}

.b-wrapper {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    background: #fff;
}