.CIContainer {
  position: relative;
  z-index: 999;
  user-select: none;
}
.CIContainer .fixedCurrentOverlay {
  padding: 0 0.09rem;
  display: flex;
  height: 0.32rem;
  width: auto;
  align-items: center;
  border-radius: 0.02rem;
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0.4rem;
  right: 0.16rem;
}
.CIContainer .fixedCurrentOverlay .overlayIcon {
  margin-right: 0.04rem;
  background-color: rgba(255, 255, 255, 1);
  display: inline-block;
  margin-right: 0.06rem;
  width: 0.18rem;
  height: 0.18rem;
  -webkit-mask-size: cover;
  -webkit-mask-repeat: no-repeat;
}
.overlayIcon.windIcon {
  -webkit-mask-image: url(../../common/img/overlay/windSvg.svg);
}
.overlayIcon.tempIcon {
  -webkit-mask-image: url(../../common/img/overlay/tempSvg.svg);
}
.overlayIcon.lightningIcon {
  -webkit-mask-image: url(../../common/img/overlay/lightSvg.svg);
}
.overlayIcon.humidityIcon {
  -webkit-mask-image: url(../../common/img/overlay/humiditySvg.svg);
}
.overlayIcon.rainIcon {
  -webkit-mask-image: url(../../common/img/overlay/rainSvg.svg);
}
.CIContainer .fixedCurrentOverlay .overlayText {
  font-size: 0.12rem;
  font-family: PingFangSC-Medium;
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
}
.leftBottomArea {
  position: fixed;
  left: 16px;
  bottom: 76px;
  width: auto;
}
.leftBottomArea .switchArea {
  display: flex;
  align-items: center;
}
.leftBottomArea .switchArea .switchIcon {
  cursor: pointer;
  margin-right: 4px;
  width: 24px;
  height: 12px;
  transition: all 0.2s;
}
.leftBottomArea .switchArea .switchIcon.opened {
  background: no-repeat url('../../common/img/overlay/windHide.png') center
    center/ 100% 100%;
}
.leftBottomArea .switchArea .switchIcon.hided {
  background: no-repeat url('../../common/img/overlay/windOpen.png') center
    center/ 100% 100%;
}
.leftBottomArea .switchArea .switchIcon.opened:hover {
  background: no-repeat url('../../common/img/overlay/windHideHover.png') center
    center/ 100% 100%;
}
.leftBottomArea .switchArea .switchIcon.hided:hover {
  background: no-repeat url('../../common/img/overlay/windOpenHover.png') center
    center/ 100% 100%;
}
.leftBottomArea .switchArea .switchText {
  font-size: 12px;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
}
.leftBottomArea .infoBox {
  margin-top: 10px;
  padding: 6px 8px;
  font-size: 14px;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(204, 204, 204, 1);
  background-color: #00000099;
  border-radius: 2px;
}
.xIcon {
  position: absolute;
  right: -18px;
  top: -18px;
  width: 18px;
  height: 18px;
  background: #000005 no-repeat url(../../common/img/overlay/xIcon.png) center
    center/10px 10px;
  border-radius: 18px;
  cursor: pointer;
}
/* 当前定位点 start*/
.signDot {
  position: fixed;
  width: 0.2rem;
  height: 0.2rem;
  justify-content: center;
  align-items: center;
  z-index: 999;
  border-radius: 0.34rem;
  transform: translate(-50%, -50%);
  display: none;
}
.signDot .radius {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.signDot .radius.dot3 {
  width: 0.2rem;
  height: 0.2rem;
  background-color: #099dff;
  border-radius: 0.2rem;
  border: 0.02rem solid white;
  box-sizing: border-box;
}
/* 当前定位点 end*/
/* 点位气泡 start */
.overlayTipArea {
  display: inline-table;
  padding: 0.09rem 0.42rem 0.09rem 0.12rem;
  background-color: #000000cc;
  position: fixed;
  z-index: 999;
  transform: translate(-50%, -100%);
  margin-top: -0.18rem;
  border-radius: 0.04rem;
  font-size: 0.14rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
}
.overlayTipArea.nodata{
  padding: 0.09rem 0.12rem 0.09rem 0.12rem;
}
.currentInfo span {
  vertical-align: middle;
}
.overlayTipArea .map_more_size {
  font-size: 0.18rem;
  position: absolute;
  right: 0.12rem;
  top: 50%;
  color: rgba(9, 157, 255, 1);
  transform: translateY(-50%);
}
.overlayTipArea .arrow {
  width: 0;
  height: 0;
  border-width: 5px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.8) rgba(255, 255, 255, 0) rgba(255, 255, 255, 0)
    rgba(255, 255, 255, 0);
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
}
.overlayTipArea .overlayInfoBox{
  white-space: nowrap;
}
/* 点位气泡 end */
