.hidden{
    display: none;
}
.container{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    position: fixed;
    transition: all .2s;
    /* background:#B2EBF2; */
    overflow: hidden;
}
.container.top{
    top:0px;
}
.container.bottom{
    bottom:0px;
}

.container .b-wrapper{
    /* padding:10px 0; */
    width: 100%;
    height: 100%;
    position: absolute;
    top:55% !important;
    overflow: unset !important;
    transition: all .2s;
    background-color: transparent !important;
    z-index: 1001;
}
.b-scroll-content{
    margin: 0 auto;
    width: 91.47%;
    box-sizing: border-box;
}
.weatherContainer{
    margin: 0 auto;
    width: 100%;
    box-sizing: border-box;
    background: transparent;
    /* overflow-y: scroll; */
    padding: 0.16rem 0.16rem 0 0.16rem;
    position: fixed;
    bottom: 0;
    z-index: 999;
}
.weatherContainer.wholeShow{
    height:100%;
    overflow-y:scroll;
    background-color:rgba(250,251,253,1);
}
.boxs .cancelArea{
    width:0.28rem;
    height:0.28rem;
    position: absolute;
    top:-0.36rem;
    right:0;
}
.boxs .cancelArea .cacelBtn{
    width:100%;
    height:100%;
    text-align: center;
    line-height: 100%;
    background-color: #FFFFFF;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.boxs .cancelArea .cacelBtn .cancelIcon{
    width:0.16rem;
    height:0.16rem;
}
.boxs .cancelArea .verticalLine{
    display: inline-block;
    width: 0.02rem;
    height: 0.1rem;
    background-color: white;
    position: absolute;
    left: 50%;
    bottom: -0.09rem;
    margin-left: -0.01rem;
}
.boxs{
    padding:0 0 0.16rem 0;
    font-size:0.12rem;
    text-align: left; 
    position: relative;
    /* min-height:6.97rem;   */
}
.boxs .item{
    border-radius: 0.08rem;
    background-color: #FFFFFF;
    margin-bottom:0.08rem;
    box-shadow: 0px 6px 20px 2px rgba(47,52,186,0.06);
}
.boxs .addArea{
    padding: 0.1rem 0.16rem 0.08rem 0.16rem;
    height:0.6rem;
    width: 100%;
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: row;
}
.boxs .addArea > div{
    flex: auto;
    overflow: hidden;
}
.boxs .addArea > div > p {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    word-break: keep-all;
}
.boxs .addArea .address{
    margin-bottom: 0.03rem;
    font-size: 0.16rem;
    color: #292B42;
    font-family: PingFangSC-Semibold;
    font-weight: 600;
    line-height: 0.22rem;
}
.boxs .addArea .detail{
    font-size: 0.12rem;
    color: #858994;
    line-height: 0.17rem;
}
.boxs .addArea .mapIcon{
    width:0.4rem;
    height:0.4rem;
    position: absolute;
    right:0.25rem;
    top:0.16rem;
}
.boxs .addArea .relocation{
    width: 0.84rem;
    height: 0.28rem;
    margin-top: 0.07rem;
    border: 1px solid rgba(215,216,224,1);
    border-radius: 0.18rem;
    background: rgba(255,255,255,0.2);
    color: rgba(41,43,66,1);
    line-height: 0.18rem;
    font-size: 0.13rem;
    font-weight: 400;
}
.boxs .windInfo{
    padding-top: 0.06rem;
}
.boxs .windInfo.pad{
  padding-bottom:0.34rem;
}
.boxs .windInfo .titleArea{
    padding:0 0.16rem;
    width:100%;
    height:0.44rem;
    box-sizing: border-box;
    position: relative;
}
.boxs .windInfo .titleArea .detailText{
    position: absolute;
    right:0.16rem;
    top:0.0rem;
    height:0.4rem;
    font-size: 0rem;
}
.boxs .windInfo .titleArea .detailText .windSpeed{
    margin-right:0.12rem;
    display: inline-block;
    font-size:0.12rem;
    color:#3C3E47;
    line-height: 0.44rem;
    font-family:PingFangSC-Regular;
}
.boxs .windInfo .titleArea .detailText .windDirection{
    display: inline-block;
    font-size:0.12rem;
    color:#3C3E47;
    line-height: 0.44rem;
    font-family:PingFangSC-Regular;
}
.boxs .windInfo .titleArea .dateText{
    display: inline-block;
    font-size:0.12rem;
    color:#222A42;
    line-height: 0.44rem;
    font-weight: 600;
    font-family:PingFangSC-Semibold;
}
.boxs .windInfo .titleArea .title{
    display: inline-block;
    font-size:0.15rem;
    color:#292B42;
    line-height: 0.44rem;
    font-weight: 600;
    font-family:PingFangSC-Semibold;
}
.boxs .windInfo .titleArea .timeArea{
    font-size: 0rem;
    width: auto;
    height: 0.44rem;
    line-height: 0.44rem;
    position: absolute;
    right: 0.16rem;
    top: 0rem;
    display: inline-block;
}
.boxs .windInfo .titleArea .timeArea .timeBtn{
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    padding:0 0.14rem;
    height: 0.24rem;
    line-height: 0.24rem;
    text-align: center;
    font-size: 0.12rem;
    color: #26272E;
    background-color: rgba(244,245,246,1);
    border-radius: 0.12rem;
}
.boxs .windInfo .titleArea .timeArea .timeBtn.active{
    color:#FFFFFF;
    background: linear-gradient(270deg,rgba(41,118,255,1) 0%,rgba(21,152,250,1) 100%);
}
.boxs .windInfo .titleArea .timeArea .timeBtn.dayTime{
    margin-right:0.04rem;
}
.boxs .windInfo .content{
    padding: 0 0.16rem;
    width:3.43rem;
    /* border-top:1px dashed #EFEFF4; */
    position: relative;
    box-sizing: border-box;
}
.boxs .windInfo .content.pad{
  padding-bottom:0.16rem;
}
.boxs .windInfo .content div{
    position: relative;
    z-index: 9;
}
.boxs .windInfo .content .restoreIcon{
    position: absolute;
    right: 0.06rem;
    bottom: 0.3rem;
    width: 0.6rem;
    height: 0.6rem;
    z-index: 9999;
}
.boxs .windInfo .moreInfoBox{
    width: 100%;
    height: 0.36rem;
    text-align:center;
}
.boxs .windInfo .moreInfoBox .moreBtnText{
    display: inline-block;
    font-size:0.14rem;
    line-height: 0.36rem;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(9,157,255,1);
}
.boxs .windInfo .weatherData{
    width:100%;
    /* padding-left:0.14rem;
    padding-right:0.14rem; */
    overflow-x: auto;
    box-sizing: border-box;
    font-size: 0rem;
    padding-bottom:0.29rem;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
    position: relative;
}
.boxs .windInfo .weatherData .everyDay{
    display: inline-block;
    width: 33%;
    text-align: center;
    color:#48516C;
    /* margin-right:0.28rem; */
}
.boxs .windInfo .weatherData .everyDay:first-child{
    /* color:#AAB2C8; */
    opacity:0.5;
}
.boxs .windInfo .weatherData .everyDay:last-child{
    margin-right:0rem;
}
/* .boxs .windInfo .weatherData .everyDay{
    width:0.36rem;
} */
.boxs .windInfo .weatherData .everyDay .normalText{
    margin-bottom:0.08rem;
    font-size: 0.12rem;
    /* color:#48516C; */
    line-height:0.17rem;
}
.boxs .windInfo .weatherData .everyDay .weatherDataBlock{
    margin-bottom:0.14rem;
    height:0.8rem;
}
.boxs .windInfo .weatherData .everyDay .wIcon{
    display: inline-block;
    margin-bottom:0.08rem;
    font-size: 0.20rem;
}
.boxs .windInfo .weatherData .echartForWeather{
    position: absolute;
    top:1.06rem;
    left: 0.40875rem;
    height:0.8rem;
    /* width:9.32rem; */
    width:2.6225rem;
}
.boxs li{
    width:100%;
    height:20px;
}

/* echarts */
.dotTip {
    display: inline-block;
    margin-right: 5px;
    border-radius: 10px;
    width: 10px;
    height: 10px;
    background-color: #2A86FF;
}
.imgTip {
    width: 18px;
    height: 15px;
    margin-left: -3px;
    transform: rotate(30deg);
}
.titleTip {
    color: #BBBFC7;
    font-size: 0.14rem;
}
.textTip {
    color: #3C3E47;
    font-size: 0.15rem;
    font-weight: 600;
}
.echarts-for-react > div:nth-child(2) {
    -webkit-appearance: none;
    -moz-box-shadow:0px 0px 8px 0px rgba(9, 10, 12, 0.1);
    -webkit-box-shadow:0px 0px 8px 0px rgba(16,17,20,0.1);
    box-shadow:0px 0px 8px 0px rgba(16,17,20,0.1);
}
/* 适配某些机型会将canvas作为一个object站在第二个元素 */
.echarts-for-react > div:nth-child(3) {
    -webkit-appearance: none;
    -moz-box-shadow:0px 0px 8px 0px rgba(9, 10, 12, 0.1);
    -webkit-box-shadow:0px 0px 8px 0px rgba(16,17,20,0.1);
    box-shadow:0px 0px 8px 0px rgba(16,17,20,0.1);
}
/* 新增样式 */
.changeInfoBtn{
  width: 100%;
  height: 0.68rem;
  font-size: 0.14rem;
  color: rgba(9,157,255,1);
  text-align: center;
  line-height: 0.68rem;
  position: fixed;
  left: 0;
  bottom: 0;
  background: linear-gradient(180deg,rgba(255,255,255,0) 0%,rgba(255,255,255,0.9) 12%,rgba(255,255,255,1) 100%);
}
.changeInfoBtn.fadeIn{
  animation:fadeIn .4s;
  -moz-animation:fadeIn .4s; /* Firefox */
  -webkit-animation:fadeIn .4s; /* Safari and Chrome */
  -o-animation:fadeIn .4s; /* Opera */
}
@keyframes fadeIn
{
from {opacity:0}
to {opacity:1}
}

@-moz-keyframes fadeIn /* Firefox */
{
from {opacity:0}
to {opacity:1}
}

@-webkit-keyframes fadeIn /* Safari 和 Chrome */
{
from {opacity:0}
to {opacity:1}
}

@-o-keyframes fadeIn /* Opera */
{
from {opacity:0}
to {opacity:1}
}