.locationArea p {
  margin: 0;
}
.locationArea {
  padding: 0 16px;
  position: fixed;
  top: 16px;
  right: 16px;
  display: flex;
  align-items: center;
  width: 350px;
  height: 36px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 2px;
  box-sizing: border-box;
  z-index: 999;
}
.locationArea .inputDegBox {
  margin-right: 16px;
  display: flex;
  align-items: center;
}
.locationArea .inputDegBox p {
  display: inline-block;
  font-size: 14px;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.8);
  text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.74);
}
.locationArea .inputDegBox .inputVal {
  padding: 0px 8px;
  width: 60px;
  height: 24px;
  font-size: 12px;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  border: none;
  border-radius: 2px;
  border: 1px solid rgba(0, 204, 255, 1);
  background: rgba(0, 0, 0, 1);
  box-sizing: border-box;
}
.locationArea .toPosBtn {
  width: 90px;
  height: 24px;
  line-height: 24px;
  cursor: pointer;
  background: rgba(0, 204, 255, 0.4);
  border-radius: 2px;
  border: 1px solid rgba(0, 204, 255, 1);
  text-align: center;
  font-size: 14px;
  font-family: PingFangTC-Regular;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  opacity: 0.5;
  user-select: none;
}
.locationArea .toPosBtn:hover {
  opacity: 1;
}
