.PlayButton.play {
  width: 0.5rem;
  height: 0.4rem;
  cursor: pointer;
  background: rgba(9, 157, 255, 1);
  position: relative;
}
.PlayButton.play:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 0px;
  height: 0px;
  border: none;
  border-color: transparent;
  border-left-color: white;
  border-style: solid;
  border-width: 0.09rem 0 0.09rem 0.13rem;
  transform: translate(-50%, -50%);
}
.PlayButton.pause {
  width: 0.5rem;
  height: 0.4rem;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.6);
  position: relative;
}
.PlayButton.pause:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  transform: translate(-50%, -50%);
  width: 4px;
  height: 16px;
  background-color: rgba(9, 157, 255, 1);
  box-shadow: 8px 0px 0px rgba(9, 157, 255, 1);
  margin-left: -4px;
}
